<template>
  <div class="home container">
    <header-component></header-component>
    <md-tabs class="md-transparent navegation" md-alignment="fixed" >
      <md-tab id="tab-home" md-label="Home">
        <main-component></main-component>
      </md-tab>
      <md-tab id="tab-posts"  md-label="Posts">
        <artigos-component></artigos-component>
      </md-tab>
      <md-tab id="tab-portfolio" class="md-xsmall-size-100" md-label="Portfólio">
          <h1>Template Bootstrap + Webpack</h1>
          <a href="https://dionmaicon.github.io/boostrap4_webpack_template/" target="_blank">
            <img  class="imagens_portfolio" src="../assets/boostrap4_webpack_template.png" alt="Bootstrap 4 template">
          </a>
      </md-tab>
      <md-tab id="tab-curriculum" md-label="Curriculum">
        <curriculum-component></curriculum-component>
      </md-tab>
    </md-tabs>
    <footer-component></footer-component>
  </div>
</template>
<script>
import HeaderComponent from "./Header/HeaderComponent";
import FooterComponent from "./Footer/FooterComponent";
import ArtigoComponent from "./Artigo/IndexArtigoComponent";
import PrincipalComponent from "./Principal/IndexPrincipalComponent";
import CurriculumComponent from "./Curriculum/CurriculumComponent";

export default {
  name: 'HomeComponent',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  components:{
    'header-component': HeaderComponent,
    'footer-component': FooterComponent,
    'artigos-component': ArtigoComponent,
    'main-component': PrincipalComponent,
    'curriculum-component': CurriculumComponent
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.imagens_portfolio {
  max-width: 350px;
  max-height: 300px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.long{
  margin-bottom: 1500px;
}
</style>



